<template>
<div class="login">
    <div class="top-img" v-if="bl">
        <img src="https://sta.gtimg.com/mobile/static/images/top_red_13.png" alt="" >
    </div>
    <div class="logo">
        <img src="https://sta.gtimg.com/mobile/static/images/qd_icon.png" alt="">
    </div>
    <div class="login-form">
        <btn icon="iconfont icon-ren" type="text" text="请输入账号" @click="hide" @user="getUser"></btn>
        <btn icon="iconfont icon-suo" type="password" text="请输入密码" @click="hide" @user="getPass"></btn>
    </div>
    <span @click='goReg'>还没账号？去注册</span>
    <div class="subm" @click="login">登录</div>
    <h4>{{errorMess}}</h4>
</div>
</template>

<script>

import btn from '../components/btn'

export default {
  name: 'login',
  components: {
      btn
  },
  data(){
      return {
          bl:true,
          username:'',
          password:'',
          errorMess:''
      }
  },
  methods:{
      hide(data){
          this.bl=data
      },
      getUser(data){
          this.username=data
        //   console.log(this.user)
      },
      getPass(data){
          this.password=data
        //   console.log(this.password)
      },
      login(){
          this.$axios({
          url:'/api/login',
          data:{username:this.username,password:this.password},
          method:'post',
        }).then(
          res => {
            // console.log('login',res.data);
            if(res.data.err == 0){
              // 1.种token
              window.localStorage.setItem('user',JSON.stringify(res.data))
              // 2.跳转到之前,没有之前跳转到user
              if(this.$route.query.p){
                this.$router.replace(this.$route.query.p)
              }else{
                this.$router.replace('/user')
              }
            }else{
             this.errorMess=res.data.msg
            }
          }
        )
      },
      goReg(){
          this.$router.push('/reg')
      }
  }
 
}
</script>

<style scoped>
.login{
    background: #fff;
    height: 13.34rem;
}
.top-img{
    height: 1.6rem;
}
.top-img img{
    width: 100%;
    height: 100%;
    display: block;
}
.logo{
    height: 1.92rem;
    display: flex;
    background: #fff;
    border-radius: 0.2rem 0.2rem 0 0;
    position: relative;
    top: -0.2rem;
}
.logo img{
    margin: auto;
}
.login-form{
    background: #fff;
    height: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-top: -0.2rem;
    padding: 0.6rem 0;
}
.subm{
    width: 6.22rem;
    height: 0.96rem;
    background:#f58183;
    text-align: center;
    line-height: 0.96rem;
    border-radius: 0.48rem;
    color: #fff;
    font-size: 40px;
    margin-left: 0.64rem;
    margin-top: 0.4rem;
}
span{
    color: skyblue;
    margin-left:5rem;
}
h4{
    color:red;
    width: 100%;
    text-align: center;
    margin-top: 0.2rem;
}
</style>