<template>
  <div class="girl">
    <loading v-if='$root.bLoading'></loading> 
    <swiper :data='swiper'></swiper>
    <!-- 导航 -->
    <div class="nav">
      <dl v-for="(val,index) of nav" :key='index'>
        <dt :class='val.icon'></dt>
        <dd>{{val.text}}</dd>
      </dl>
    </div>
    <Hot :data='list1' text='热门小说'/>
    <book-list :data='list3' text='新书抢先'/>
    <Hot :data='list2' text='限时免费'/>
    <book-list :data='list4' text='畅销完本'/>


  </div>
</template>

<script>

import swiper from '../components/swiper'
import Hot from '../components/hotList.vue'
import bookList from '../components/bookList.vue'
import loading from "./loading"

export default {
  name: 'girl',
  components: {
    swiper,Hot,bookList,loading
  },
  data(){
    return {
      nav:[
        {icon:'iconfont icon-fenlei',text:'分类'},
        {icon:'iconfont icon-icon-',text:'排行榜'},
        {icon:'iconfont icon-mianfei',text:'免费'},
        {icon:'iconfont icon-shu',text:'完本'},
        {icon:'iconfont icon-huangguan',text:'大神'},
        ],
      swiper:[],
      list:[],
      type:true
    }
  },
  methods:{
    getData(){
       this.$axios({
        url:'/api/goods/banner',
      }).then(
        res=>this.swiper=res.data.data
      )
      
      this.$axios({
        url:'/api/goods/list',
        params:{_limit:18}
      }).then(
        res=>this.list=res.data.data
      )
    }
  },
  mounted(){
      this.getData()
    },
  computed:{
    list1(){
      return this.list.slice(0,6)
    },
    list2(){
      return this.list.slice(6,12)
    },
    list3(){
      return this.list.slice(3,6)
    },
    list4(){
      return this.list.slice(7,10)
    },
  }  
}
</script>

<style scoped>
  /* nav */
  .nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height:1.64rem;
    background: #fff;
    margin: 0.2rem 0;
  }
  .nav dl{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .nav dl dt{
    font-size: 40px;
  }
  .nav dl:nth-child(1){
    color:skyblue
  }
   .nav dl:nth-child(2){
    color:red
  }
   .nav dl:nth-child(3){
    color:orange
  }
   .nav dl:nth-child(4){
    color:yellow
  }
   .nav dl:nth-child(5){
    color:purple
  }
</style>