<template>
<div class="reg">
    
    <div class="logo">
        <img src="https://sta.gtimg.com/mobile/static/images/qd_icon.png" alt="" @click='ipt'>
        <p>上传头像</p>
        <input type="file" class="file" id="icon">
    </div>
    <div class="reg-form">
        <btn icon="iconfont icon-ren" type="text" text="请输入账号" @click="hide" @user="getUser"></btn>
        <btn icon="iconfont icon-suo" type="password" text="请输入密码" @click="hide" @user="getPass"></btn>
        <span @click='goReg'>已有账号？去登录</span>
        <div class="subm" @click="reg">注册</div>
        <h4>{{errorMess}}</h4>
    </div>
    
</div>
</template>

<script>

import btn from '../components/btn'
export default {
  name: 'reg',
  components: {
      btn
  },
  data(){
      return {
        bl:true,
        username:'',
        password:'',
        errorMess:''
      }
  },
  methods:{
      hide(data){
          this.bl=data
      },
      getUser(data){
          this.username=data
      },
      getPass(data){
          this.password=data
      },
      ipt(){
          let input = document.getElementById('icon')
          input.click()
      },
      reg(){
        let formData = new FormData();
        formData.append('username',this.username);
        formData.append('password',this.password);
        
        let iconInputFile = document.getElementById('icon');
        formData.append('icon',iconInputFile.files[0]);//取出input file的文件体
        
        this.$axios({
          url:'/api/reg',
          method:'post',
          data:formData
        }).then(
          res => {
              console.log(res)
            if(res.data.err === 0){
              this.$router.push('/login')
            }else{
              this.errorMess=res.data.msg
            }
          }
        )
      },
      goReg(){
          this.$router.push('/login')
      }
  }
 
}
</script>

<style scoped>
.reg{
    background: #fff;
    height: 12.52rem;
    padding-top: 0.8rem;
}

.logo{
    height: 1.92rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 0.2rem 0.2rem 0 0;
    position: relative;
}
.logo img{
    margin-bottom: 0.2rem;
}
.logo input{
    position: absolute;
    left: 40%;
    top: 50%;
    font-size: 60px;
    display: none;
}
.reg-form{
    background: #fff;
    height: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 0.6rem 0;
}
.subm{
    width: 6.22rem;
    height: 0.96rem;
    background:#f58183;
    text-align: center;
    line-height: 0.96rem;
    border-radius: 0.48rem;
    color: #fff;
    font-size: 40px;
}
span{
    color: skyblue;
    margin-left:5rem;
}
h4{
    color: red;
    margin-top: 0.2rem;
}
</style>