<template>
    <div class="user">
        <div class="top">
            <span @click='$router.push("/home")'>&lt;</span>
            <span>个人中心</span>
        </div>
        <!-- header -->
        <div class="header">
            <img :src="'http://localhost:9001'+user.icon" alt="">
            <p>{{user.nikename}}</p>
            <span>{{user.lv}}</span>
        </div>
        <!-- group -->
        <div class="group">
            <dl>
                <dt>{{user.month}}</dt>
                <dd>月票</dd>
            </dl>
            <dl>
                <dt>{{user.tuijian}}</dt>
                <dd>推荐票</dd>
            </dl>
            <dl>
                <dt>{{user.bi}}</dt>
                <dd>起点币</dd>
            </dl>
            <dl>
                <dt class="iconfont icon-dashang"></dt>
                <dd>去充值&gt;</dd>
            </dl>
        </div>
        <!-- layout -->
        <div class="layout">
            <userBtn icon='iconfont icon-xiaoxizhongxin' color='red' text='消息中心'/>
            <userBtn icon='iconfont icon-zuanshi' color='purple' text='会员等级'/>
            <userBtn icon='iconfont icon-xingxing' color='yellow' text='经验等级'/>
            <userBtn icon='iconfont icon-anquanzhongxinon' color='orange' text='安全中心'/>
            <userBtn icon='iconfont icon-bangzhuzhongxin' color='blue' text='帮助中心'/>
        </div>
        <!-- logout -->
        <div class="logout" @click='logout'>
            退出登录
        </div>
    </div>
</template>

<script>

import userBtn from '../components/userBtn'

export default {
  name: 'user',
  components: {
    userBtn
  },
  beforeRouteEnter(to,from,next){
      // 每次去服务器抓取最新
      axios.get('/api/user')
        .then(
          res => res.data.err === 0 ?
            next(_this=>_this.user=res.data.data) :
            next('/login')
        )      
    },
    data(){
      return {
        user:{}
      }
    },
  methods:{
    logout(){
        window.localStorage.removeItem('user');
        this.$router.push("/login")
    }
  },

}
</script>

<style scoped>
    .top{
        width: 100%;
        height: 0.88rem;
        display: flex;
        align-items: center;
        padding: 0 0.4rem;
        background: #ed424b;
        color: #fff;
    }
    .top span{
        font-size: 30px;
    }
    .top span:nth-child(1){
        margin-right: 0.4rem;
    }
    /* header */
    .header{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: #ed424b;
        padding-bottom: 0.4rem;
        color: #fff;
    }
    .header img{
        width: 1.16rem;
        height: 1.16rem;
        border-radius: 100%;
    }
    .header p{
        margin: 0.1rem 0;
        font-size: 30px;
    }
    .header span{
        background: orange;
        border-radius: 0.1rem;
        padding: 0.05rem;
    }
    /* group */
    .group{
        display: flex;
        height: 1.28rem;
        background: #fff;
    }
    .group dl{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .group dl dt{
        font-size: 40px;    
    }
    .group dl dd{
        font-size: 26px;
        color: #969ba3;
    }
    .group dl:nth-child(4){
        font-size: 26px;
        color: red;
    }
    .group dl:nth-child(4) dd{
        font-size: 26px;
        color: red;
    }
    /* layout */
    .layout{
        background: #fff;
        margin-top: 0.4rem;
        padding-left: 0.4rem;
    }
    /* logout */
    .logout{
        height: 0.88rem;
        text-align: center;
        line-height: 0.88rem;
        font-size: 40px;
        color: red;
        background: #fff;
        margin-top: 0.4rem;
    }
</style>