<template>
    <div class="detail">
        <loading v-if='$root.bLoading'></loading> 
        <div class="top">
            <span @click='$router.push("/home")'>&lt;</span>
            <span>{{data.name}}</span>
        </div>
        <!-- 详情 -->
        <div class="book-layout">
            <img :src=data.book_img alt="">
            <div class="book-cell">
                <h2>{{data.name}}</h2>
                <div class="book-rand">
                    <p>{{data.author}}</p>
                    <span>{{data.lv}}</span>
                </div>
                <p>{{data.label}}</p>
                <p>{{data.num}}万字<span class="sp"></span>{{data.state}}</p>
            </div>
        </div>
        <!-- 按钮 -->
        <div class="book-detail-btn">
            <ul>
                <!-- <router-link active-class="active" tag="li">免费试读</router-link>
                <router-link active-class="active" tag="li">加入书架</router-link>
                <router-link active-class="active" tag="li">VIP订阅</router-link> -->
                <li class="active">免费试读</li>
                <li>加入书架</li>
                <li>VIP订阅</li>
            </ul>
        </div>
        <!-- 介绍 -->
        <div class="book-cont">
            <p>{{data.dsc}}</p>
        </div>
        <!-- 打赏 -->
        <div class="pay">
            <dl>
                <dd class="iconfont icon-piaoquan"></dd>
                <dt>投月票</dt>
                <span>{{data.ticket}}票</span>
            </dl>
            <dl>
                <dd class="iconfont icon-tuijian"></dd>
                <dt>投推荐票</dt>
                <span>{{data.rec}}票</span>
            </dl>
            <dl>
                <dd class="iconfont icon-dashang"></dd>
                <dt>打赏</dt>
                <span>{{data.reward}}人</span>
            </dl>
        </div>
        <!-- 作者 -->
        <div class="auth">
            <div class="antu-top">
                <h2>本书作者</h2>
            </div>
            <div class="auth-bottom">
                <div class="pic">
                    <img :src=data.author_img alt="">
                    <span>{{data.lv}}</span>
                </div>
                <div class="auth-right">
                    <h4>{{data.author}}</h4>
                    <p>暂无作者简介</p>
                </div>
            </div>
        </div>
        <!-- 标签 -->
        <div class="auth">
            <div class="antu-top">
                <h2>相关标签</h2>
            </div>
            <ul>
                <li v-for="(val,index ) of data.biaoqian" :key="index">{{val}}</li>
            </ul>
        </div>
    </div>
</template>

<script>

import loading from "./loading"

export default {
  name: 'detail',
  components: {
    loading
  },
   data(){
      return {
        data:{}
      }
    },
    mounted(){
      let collectionName=this.$route.params.collectionName;
      let _id=this.$route.params.id;
      this.$axios({
        url:`/api/goods/${collectionName}/${_id}`,
      }).then(
        res=>{
            if(this.$route.params.collectionName === 'banner'){
                this.data=res.data.data.banner_content
            }else{
                this.data=res.data.data
          
            }
        }
      )
    },

}
</script>

