<template>
<div class="Default">
  <my-header v-show='$root.bNav'></my-header>
  <router-view></router-view>
</div>
</template>

<script>

import home from '../pages/home'
import detail from '../pages/detail'
import login from '../pages/login'
import reg from '../pages/reg'
import user from '../pages/user'
import myHeader from '../layouts/header.vue'

export default {
  name: 'Default',
  components: {
    home,detail,login,reg,user,myHeader
  },
  methods:{
    checkRootData(path){
        if(/home|girl/.test(path)){
          this.$root.bNav=true
        }
        if(/detail|login|reg|user/.test(path)){
          this.$root.bNav=false
        }
       
      }
  },
  watch:{
    $route:{
      handler(nextValue){
        let path = nextValue.path
        this.checkRootData(path)
      }
    }
  }

}
</script>

